import {
  UserOutlined,
  VideoCameraOutlined,
  LaptopOutlined,
  NotificationOutlined,
  DashboardOutlined
} from '@ant-design/icons';

import Dashboard from "pages/Dashboard"

import ListUsers from "pages/Users/List"
import AddUser from "pages/Users/Add"
import EditUser from "pages/Users/Edit"

import ListCars from "pages/Cars/List"
import AddCars from "pages/Cars/Add"
import EditCars from "pages/Cars/Edit"

import ListDrivers from "pages/Drivers/List"
import AddDriver from "pages/Drivers/Add"
import EditDriver from "pages/Drivers/Edit"

import ListClients from "pages/Clients/List"
import AddClient from "pages/Clients/Add"
import EditClient from "pages/Clients/Edit"

import AddDecharge from "pages/Decharges/Add"
import UserCars from 'pages/Users/UserCars';

export default {
  appName: "GestPark v1.0.0",
  routes: [
    {
      path: '/dashboard',
      name: 'dashboard',
      label: "Dashboard",
      component: Dashboard,
      icon: <DashboardOutlined />,
      authority: ["dashboard"]
    },
    // users 
    {
      name: 'users',
      label: "Utilisateurs",
      icon: <UserOutlined />,
      routes: [
        {
          path: '/users',
          name: 'list-users',
          label: 'List des utilisateurs',
          component: ListUsers,
          exact: true
        },
        {
          path: '/users/add',
          name: 'add-user',
          label: 'Nouveau utilisateur',
          component: AddUser,
        },
        {
          path: '/users/edit/:id',
          name: 'edit-user',
          label: "Modifier l'utilisateur",
          component: EditUser,
          hideInSide: true
        },
        {
          path: '/users/cars',
          name: 'user-cars-users',
          label: 'Affecté les véhicule au utilisateurs',
          component: UserCars,
          exact: true
        },
      ]
    },
    // cars
    {
      name: 'cars',
      label: "Véhicules",
      icon: <UserOutlined />,
      routes: [
        {
          path: '/cars',
          name: 'list-cars',
          label: 'List des véhicules',
          component: ListCars,
          exact: true
        },
        {
          path: '/cars/add',
          name: 'add-cars',
          label: 'Nauveau véhicule',
          component: AddCars,
        },
        {
          path: '/cars/edit/:id',
          name: 'edit-cars',
          label: 'Modifier véhicule',
          component: EditCars,
          hideInSide: true
        }
      ]
    },
    {
      name: 'drivers',
      label: "Conducteurs",
      icon: <UserOutlined />,
      routes: [
        {
          path: '/drivers',
          name: 'list-drivers',
          label: 'List des conducteurs',
          component: ListDrivers,
          exact: true
        },
        {
          path: '/drivers/add',
          name: 'add-driver',
          label: 'Nouveau conducteur',
          component: AddDriver,
        },
        {
          path: '/drivers/edit/:id',
          name: 'edit-driver',
          label: "Modifier conducteur",
          component: EditDriver,
          hideInSide: true
        }
      ]
    },
    {
      name: 'clients',
      label: "Clients",
      icon: <UserOutlined />,
      routes: [
        {
          path: '/clients',
          name: 'list-clients',
          label: 'List des clients',
          component: ListClients,
          exact: true
        },
        {
          path: '/clients/add',
          name: 'add-client',
          label: 'Nouveau client',
          component: AddClient,
        },
        {
          path: '/clients/edit/:id',
          name: 'edit-client',
          label: "Modifier client",
          component: EditClient,
          hideInSide: true
        }
      ]
    },
    {
      name: 'decharges',
      label: "Decharges",
      icon: <UserOutlined />,
      routes: [
        {
          path: '/decharges',
          name: 'list-decharges',
          label: 'List des decharges',
          component: ListClients,
          exact: true
        },
        {
          path: '/decharges/add',
          name: 'add-decharge',
          label: 'Nouveau decharge',
          component: AddDecharge,
        }
      ]
    },
  ]
}