import * as React from "react";
import moment from "moment";
import { Button, Input, message } from "antd";

import ProTable from "@ant-design/pro-table";
import Page from "components/Page";
import { useAppStore } from "stores";
import { useHistory } from "react-router-dom";
import { _prop } from "_consts";


export default function () {
  const { api } = useAppStore()
  const history = useHistory()

  const columns = [
    {
      title: "matricule",
      dataIndex: "matricule",
      copyable: true,
      sorter: true,
    },
    {
      title: "prop",
      dataIndex: "prop",
      sorter: true,
      filters: _prop.map(i => ({ text: i.label, value: i.value })),
      hideInSearch: true
    },
    {
      title: "old_matricule",
      dataIndex: "old_matricule",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "code_gps",
      dataIndex: "code_gps",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "genre",
      dataIndex: "genre",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "marque",
      dataIndex: "marque",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "carrosserie",
      dataIndex: "carrosserie",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "color",
      dataIndex: "color",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "option",
      valueType: "option",
      dataIndex: "id",
      render: (text, row, index, action) => [
        <a
          onClick={() => {

            api.delete("/cars/" + row.id).then(() => { message.info("Bien Supprimé"); action.reload(); })
          }}
        >
          Supprimer
      </a>,
        <a
          onClick={() => {
            history.push("/cars/edit/" + row.id)
          }}
        >
          Modifier
      </a>
      ]
    }
  ];
  const actionRef = React.useRef()
  window.actionRef = actionRef
  return (
    <Page title={"Véhicules"} selectedSiderKey="list-cars">
      <ProTable
        actionRef={actionRef}
        size="small"
        search={true}
        columns={columns}
        ellipsis={true}
        request={(params, sort, filters) => {
          if (sort) {
            params["sortBy"] = Object.keys(sort)[0]
            params["sort"] = Object.values(sort)[0]
          }
          if (filters) {
            params["filterBy"] = Object.keys(filters)[0]
            params["filter"] = Object.values(filters)
          }
          return api.get("/cars", { params }).then((res) => res.data)
        }}
        pagination={{
          defaultCurrent: 1,
          pageSize: 10
        }}
      />
    </Page>
  );
}

