import { Layout, Menu } from 'antd';
import {
  UserOutlined,
  VideoCameraOutlined,
  LaptopOutlined,
  NotificationOutlined
} from '@ant-design/icons';
import config from "config"
import { useHistory } from 'react-router-dom';
import { useState } from 'react';
import { useAppStore } from 'stores';

const { Sider } = Layout;
const { SubMenu } = Menu;

export default () => {
  const history = useHistory()
  const {selectedSiderKey} = useAppStore()
  return (
    <Sider
      style={{
        overflow: 'auto',
        height: 'calc(100vh - 48px)',
        marginTop: 48,
        position: 'fixed',
        left: 0,
      }}
    >
      <Menu theme="dark" mode="inline" defaultSelectedKeys={selectedSiderKey}>
        {
          config.routes.map((r, i) => {
            if (r.routes) {
              return (
                <SubMenu key={r.name} icon={r.icon} title={r.label}>
                  {
                    r.routes.map((rr, ii) => (
                      rr.hideInSide ? undefined : <Menu.Item key={"sub-m-" + ii} onClick={() => history.push(rr.path)} key={rr.name}>{rr.label}</Menu.Item>
                    ))
                  }
                </SubMenu>
              )
            } else {
              return <Menu.Item key={"m-" + i} onClick={() => history.push(r.path)} key={r.name} icon={r.icon}>{r.label}</Menu.Item>
            }

          })
        }
      </Menu>
    </Sider>
  )
}