import React from "react"
import {
  BrowserRouter,
  Route,
  Switch,
  Link
} from "react-router-dom"
import { Layout, Menu } from 'antd';
import {
  AppstoreOutlined,
  BarChartOutlined,
  CloudOutlined,
  ShopOutlined,
  TeamOutlined,
  UserOutlined,
  UploadOutlined,
  VideoCameraOutlined,
  LaptopOutlined,
  NotificationOutlined
} from '@ant-design/icons';

import Header from "containers/Header"
import Side from "containers/Side"

import config from "config"

const { Content } = Layout;

export default function () {
  const t = []
  for (let i = 0; i < 100; i++) {
    t.push(i)
  }
  const renderSubMenuRoutes = (routes) => {
    return (
      <Switch>
        {routes.map((r, i) => (
          <Route key={i} exact={r.exact} component={r.component} path={r.path} />
        ))}
      </Switch>
    )
  }
  const renderRoutes = () => {
    return config.routes.map((r, i) => {
      return (
        <Switch>
          {
            r.routes ?
              renderSubMenuRoutes(r.routes) :
              <Route exact={r.exact} component={r.component} path={r.path} />
          }
        </Switch>
      )
    })
  }
  return (
    <BrowserRouter>
      <Layout>
        <div style={{
          position: "absolute",
          top: 0,
          left: 0,
          width: 200,
          height: 48,
          backgroundColor: "#001529",
          padding: 5,
          textAlign: "center",
        }}>
          <Link to="/">
            <h1 style={{ color: "white" }}>GestPark v1.0.0</h1>
          </Link>
        </div>
        <Side />
        <Layout style={{ marginLeft: 200 }}>
          <Header />
          <Content style={{ paddingTop: 48 }}>
            {renderRoutes()}
          </Content>
        </Layout>
      </Layout>
    </BrowserRouter >
  )
}