import React, { useRef, useState } from "react"
import { message, Steps } from 'antd';

import Page from "components/Page"
import { useAppStore } from "stores";

import { FormBuilder } from "components/UIBuilder"
import { _postes, _messages } from "_consts";

import { formDechargeItems, formCheckListItems } from "./columns"
const { Step } = Steps;

export default function () {
  const { api } = useAppStore()
  const [allData, setAllData] = useState({ step: 0 })
  window.allData = allData
  const decharge = (data) => {
    /*api.post("/decharges", data).then((res) => {
      message.info("Bien ajouter")
    })*/
    setAllData({ data: { ...allData, data }, step: 1 })
  }
  const checklist = (data) => {
    setAllData({ data: { ...allData, data }, step: 2 })
  }

  return (
    <Page title="Nauveau decharge">
      <div style={{ display: "flex", flexDirection: "column" }}>
        <Steps current={allData.step}>
          <Step title="Affectation client" description="" />
          <Step title="Checklist Conducteur" description="" />
          <Step title="Terminé" description="" />
        </Steps>
        <div><hr /></div>
        <div style={{ flex: 1, }}>
          {allData.step === 0 &&
            <FormBuilder formItems={formDechargeItems} onFinish={decharge} />
          }
          {allData.step === 1 &&
            <FormBuilder formItems={formCheckListItems} onFinish={checklist}
              initialValues={
                {
                  cle_vehicule: 1,
                  starts: 5,
                }
              }
            />
          }
          <hr />
          checklist
        </div>


      </div>,


    </Page >
  )
}