import React, { useEffect, useState } from "react"
import { message, Transfer } from 'antd';

import Page from "components/Page"
import { useAppStore } from "stores";

import { _postes, _messages } from "_consts";
import SearchInput from "components/SearchInput";

const mockData = [];
for (let i = 0; i < 20; i++) {
  mockData.push({
    key: i.toString(),
    matricule: `content${i + 1}`,
    description: `description of content${i + 1}`,
  });
}
export default function () {
  const { api } = useAppStore()
  const [cars, setCars] = useState([])
  const [carGroupe, setCarGroup] = useState("vh")
  const [data, setData] = useState({})
  const [selectedKeys, setSelectedKey] = useState([])
  const [targetKeys, setTargetKey] = useState([])

  const affecte = (data) => {
    api.post("/auth/users/cars", data).then((res) => {
      message.info("Bien ajouter")
    })
  }

  useEffect(() => {
    api.get("allcars").then(({ data }) => {
      const cars = data.data.map(car => ({ ...car, key: car.id }))
      setCars(cars)
    })
  }, [carGroupe])

  const handleChange = (nextTargetKeys, direction, moveKeys) => {
    setTargetKey(nextTargetKeys)
    console.log('targetKeys: ', nextTargetKeys);
    console.log('direction: ', direction);
    console.log('moveKeys: ', moveKeys);
  };

  const handleSelectChange = (sourceSelectedKeys, targetSelectedKeys) => {
    setSelectedKey([...sourceSelectedKeys, ...targetSelectedKeys])
    console.log('sourceSelectedKeys: ', sourceSelectedKeys);
    console.log('targetSelectedKeys: ', targetSelectedKeys);
  };


  return (
    <Page title="Affecté des vehicules au utilisateur" selectedSiderKey="user-cars-users">
      <SearchInput
        url="auth/users/search"
        style={{ width: 200 }}
        mapOptionToString={c => c.firstname + " " + c.lastname}
        onSelect={(value, option) => {
          setData({ user_id: option.id })
        }}
        onChange={(value, option) => {
          if (option) {
            setData({ user_id: option.id })
          } else {
            setData({ user_id: null })
          }
        }}
        placeholder="Utilisateur"
      />
      <hr />
      <Transfer
        dataSource={cars}
        titles={['Tout', 'Affecté']}
        targetKeys={targetKeys}
        selectedKeys={selectedKeys}
        onChange={handleChange}
        onSelectChange={handleSelectChange}
        render={item => item.code_gps + " | " + item.matricule}
        style={{ marginBottom: 16}}
        listStyle={{
          width: 250,
          height: 250,
        }}
        disabled={false}
      />
    </Page>
  )
}